.login-bg {
  width: 100%;
  height: 100%;
  background: url(../../static/media/bg.64a51333.png) no-repeat;
  background-size: 100% 100%;
  color: #fff;
  font-size: 0.3rem;
}

.login-bg input::-webkit-input-placeholder {
  color: #888;
}

.login-bg .info {
  width: 4.8rem;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -2.4rem;
  margin-top: -2.88rem;
}

.login-bg .info .system-name {
  width: 2rem;
}

.login-bg .info .tel-input {
  overflow: hidden;
  margin-top: 0.4rem;
  margin-bottom: 0.35rem;
}

.login-bg .info .tel-input div:first-child {
  margin-bottom: 0.1rem;
  font-size: 0.3rem;
}

.login-bg .info .code-input {
  overflow: hidden;
  margin-bottom: 1.3rem;
}

.login-bg .info .code-input div:first-child {
  margin-bottom: 0.1rem;
  font-size: 0.3rem;
}

.login-bg .info .form-control {
  float: left;
  width: 4.6rem;
  height: 0.7rem;
  outline: 0;
  padding-left: 0.2rem;
  font-size: 0.25rem;
}

.login-bg .info .form-control.verify-code {
  width: 2.6rem;
}

.login-bg .info .form-control.verify-btn {
  margin-left: 0.08rem;
  padding-left: 0;
  width: 1.9rem;
  line-height: 0.7rem;
  text-align: center;
  background-color: #06cc7e;
  font-size: 0.3rem;
}

.login-bg .info .btn-submit {
  font-size: 0.3rem;
  width: 2.4rem;
  height: 0.7rem;
  line-height: 0.7rem;
}

.first-login .info .code-input {
  margin-bottom: 0.3rem;
}

.first-login .info .login-way {
  margin-bottom: 0.8rem;
  font-size: 0.25rem;
}

.first-login .info .login-way div {
  display: inline-block;
  text-decoration: underline;
}

.first-login .info .login-way div:first-child {
  width: 3rem;
}

.first-login .info .login-way div:last-child {
  width: 1.8rem;
  text-align: right;
}

.vaccine-app-forget {
  background-image: none;
  background-color: #fff;
  color: #333;
}

.vaccine-app-forget .info {
  width: 5.8rem;
  margin-top: -5rem;
  margin-left: -2.9rem;
}

.vaccine-app-forget .info input::-webkit-input-placeholder {
  color: #999;
  font-size: 0.25rem;
}

.vaccine-app-forget .info .promote {
  margin: -0.25rem 0 0.4rem;
  font-size: 0.24rem;
  color: #ff7043;
}

.vaccine-app-forget .info .title {
  color: #06cc7e;
  font-size: 0.45rem;
  text-align: center;
}

.vaccine-app-forget .info .code-input {
  margin-bottom: 0.9rem;
}

.vaccine-app-forget .info .form-control {
  width: 5.8rem;
  border: 0.01rem solid #ccc;
  box-sizing: border-box;
}

.vaccine-app-forget .info .form-control.verify-code {
  width: 3.2rem;
}

.vaccine-app-forget .info .form-control.verify-btn {
  margin-left: 0.18rem;
  width: 2.4rem;
  color: #fff;
  border: 0;
}